.producto {
  display: flex;
  flex-direction: column;
  width: 100%;
 height: 100%;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
 border: solid black 1px;
  margin: 0.5rem 0;
}

.imagen{
    border-bottom: 1px solid gray;
}

.transparente {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #fff;
  transition: all 0.5s;
  background-color: #0ad4fa;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.producto:hover .transparente,
.producto:active .transparente {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.producto:hover img,
.producto:active img {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

